header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-inline: 5%;
  padding: 0.75rem;
}

.header-section-1 {
  display: flex;
  align-items: center;
}

.logo {
  padding-left: 0.8rem;
}

.header-section-2 {
  display: flex;
  align-items: center;
}

.nav-links {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 150vh;
  width: 100vw;
  z-index: 2;
  background-color: hsla(0deg, 0%, 0%, 0.75);
}

.nav-links-container {
  background-color: hsl(0deg, 0%, 100%);
  width: 50%;
  height: 150vh;
}

.close-icon {
  padding: 1rem;
}

.menu-list {
  padding: 1rem;
}
.menu-list li {
  position: relative;
  padding-bottom: 1.5rem;
}
.menu-list li::before {
  content: " ";
  position: absolute;
  bottom: 0;
  height: 4px;
  width: 100%;
  left: 0;
  background-color: hsl(26deg, 100%, 55%);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.menu-list li:hover::before {
  opacity: 1;
}

.menu-link {
  font-size: 1.2rem;
  color: hsla(0deg, 0%, 0%, 0.75);
}

.header-cart {
  padding-right: 0.75rem;
  cursor: pointer;
}

.avatar {
  width: 2rem;
  cursor: pointer;
  transition: 100ms;
}

.avatar:hover {
  border: 0.15rem solid hsl(26deg, 100%, 55%);
  border-radius: 50%;
}

.images-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product-images-container {
  width: 100%;
  position: relative;
  margin-top: 1rem;
}
.product-images-container .product-img {
  width: 100%;
  cursor: pointer;
}

.carousel-container {
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.chevron-container {
  background-color: hsl(0deg, 0%, 100%);
  width: 2rem;
  text-align: center;
  border-radius: 50%;
  padding: 0.3rem 0.3rem;
}

.chevron:hover {
  color: hsl(26deg, 100%, 55%);
}

.left-chevron {
  margin-left: 1rem;
  cursor: pointer;
}

.right-chevron {
  margin-right: 1rem;
  cursor: pointer;
}

.thumbnail-images-container {
  display: none;
}

.product-full-image-container {
  width: 100%;
  height: 300px;
}

.text-container {
  margin: 1.2rem 1rem 0;
  width: 90%;
}

.info-container {
  display: flex;
  flex-direction: column;
}
.info-container .title {
  font-size: 1.5rem;
  color: hsl(26deg, 100%, 55%);
}
.info-container .product-name {
  font-size: 2rem;
  margin-top: 1.2rem;
}
.info-container .description {
  margin-top: 1rem;
  color: hsl(219deg, 9%, 45%);
  line-height: 1.5;
}

.price-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.75rem;
}
.price-container .net-price {
  font-size: 1.75rem;
}
.price-container .net-price .discount {
  font-size: 1rem;
  background-color: hsl(25deg, 100%, 94%);
  color: hsl(26deg, 100%, 55%);
  border-radius: 25%;
  padding: 0.2rem;
  vertical-align: middle;
  margin-left: 0.25rem;
}
.price-container .gross-price {
  font-size: 1.2rem;
  color: hsl(220deg, 14%, 75%);
  text-decoration: line-through;
}

.buy-quantity-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
}
.buy-quantity-container .quantity-container {
  display: flex;
  margin-top: 0.5rem;
  padding: 1rem 0.5rem;
  width: 95%;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(223deg, 64%, 98%);
  border-radius: 0.8rem;
}
.buy-quantity-container .quantity-container .counter {
  cursor: pointer;
  transition: 300ms;
}
.buy-quantity-container .quantity-container .counter:hover {
  opacity: 0.5;
}
.buy-quantity-container .quantity-container span {
  font-weight: 700;
}
.buy-quantity-container .add-to-cart {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  padding: 1rem 0.5rem;
  width: 95%;
  background-color: hsl(26deg, 100%, 55%);
  color: hsl(0deg, 0%, 100%);
  border: none;
  border-radius: 0.8rem;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 2px 15px 8px hsl(25deg, 100%, 94%);
  transition: 300ms;
}
.buy-quantity-container .add-to-cart .cart-icon {
  margin-right: 0.5rem;
}
.buy-quantity-container .add-to-cart:hover {
  opacity: 0.7;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }

  .nav-links {
    display: flex;
    position: relative;
    background: none;
    height: 1.25rem;
    width: 10vw;
    padding-left: 2.5rem;
  }

  .nav-links-container {
    background: none;
  }
  .nav-links-container .menu-list {
    display: flex;
    padding: 0;
    gap: 1rem;
  }
  .nav-links-container .menu-list li a {
    color: hsl(219deg, 9%, 45%);
    font-weight: 400;
  }

  .close-icon {
    display: none;
  }

  .header-cart {
    margin-right: 1rem;
  }

  .avatar {
    width: 3rem;
  }

  main {
    border-top: 1px solid hsl(220deg, 14%, 75%);
  }

  .carousel-container {
    display: none;
  }

  .thumbnail {
    width: 100%;
  }

  .thumbnail-images-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1rem;
    padding: 0.5rem;
    margin-top: 1rem;
  }

  .thumbnail {
    border-radius: 1rem;
    cursor: pointer;
  }

  .thumbnail:hover {
    opacity: 0.7;
    outline: 2px solid hsl(26deg, 100%, 55%);
  }

  .active {
    border: 2px solid hsl(26deg, 100%, 55%);
    opacity: 0.7;
  }

  .quantity-info-container p {
    font-size: 1.3rem;
  }

  .delete {
    width: 1.5rem;
  }
}
@media (min-width: 1200px) {
  header {
    padding-inline: 2.5rem;
  }

  .nav-links {
    z-index: 0;
  }

  main {
    padding-top: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    margin-inline: 3rem;
  }

  .product-img {
    border-radius: 1rem;
  }

  .text-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .info-container {
    margin-bottom: 2rem;
  }

  .price-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .buy-quantity-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    margin-top: 4rem;
  }
  .buy-quantity-container .quantity-container {
    width: 40%;
  }
}
@media (min-width: 1440px) {
  .product-images-container {
    width: 85%;
  }

  .thumbnail-images-container {
    width: 85%;
  }
}
@media (min-width: 1600px) {
  header {
    padding-inline: 6rem;
  }

  main {
    margin-inline: 6rem;
  }

  .nav-links {
    display: flex;
    justify-content: space-evenly;
  }

  .menu-list li {
    margin-right: 1.5rem;
  }

  .images-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .product-images-container {
    width: 85%;
  }

  .thumbnail-images-container {
    width: 85%;
    column-gap: 2rem;
  }

  .info-container {
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .info-container .description {
    width: 60%;
  }

  .buy-quantity-container {
    width: 80%;
  }

  .price-container {
    margin-top: 4rem;
  }
}
@media (min-width: 1920px) {
  main {
    column-gap: 0;
  }

  .product-images-container {
    width: 70%;
  }

  .thumbnail-images-container {
    width: 70%;
  }

  .text-container {
    margin: 0;
  }
  .text-container .info-container .title {
    font-size: 1.8rem;
  }
  .text-container .info-container .product-name {
    font-size: 2.2rem;
  }
  .text-container .info-container .description {
    font-size: 1.1rem;
  }
}
@media (max-width: 1999px) {
  .modal {
    display: none;
  }
}
@media (min-width: 1200px) {
  .modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: hsla(0deg, 0%, 0%, 0.8);
  }

  .modal-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    gap: 1rem;
  }

  .modal-product-images-container {
    width: 50%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .modal-close {
    color: hsl(220deg, 14%, 75%);
    font-size: 1.5rem;
    align-self: flex-end;
  }

  .modal-close:hover {
    color: hsl(26deg, 100%, 55%);
    cursor: pointer;
  }

  .modal-img {
    width: 90%;
    border-radius: 1rem;
  }

  .modal-carousel-container {
    position: absolute;
    top: 50%;
    width: 47%;
    display: flex;
    justify-content: space-between;
  }

  .modal-chevron-container {
    background-color: hsl(0deg, 0%, 100%);
    width: 2rem;
    text-align: center;
    border-radius: 50%;
    padding: 0.3rem 0.3rem;
  }

  .modal-left-chevron, .modal-right-chevron {
    cursor: pointer;
  }

  .modal-left-chevron:hover, .modal-right-chevron:hover {
    color: hsl(26deg, 100%, 55%);
  }

  .modal-thumbnail-images-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  .modal-thumbnail {
    border-radius: 1rem;
    width: 70%;
    cursor: pointer;
    justify-self: center;
  }

  .modal-thumbnail:hover {
    opacity: 0.8;
    border: 2px solid hsl(26deg, 100%, 55%);
  }
}
@media (min-width: 1600px) {
  .modal-product-images-container {
    width: 40%;
  }

  .modal-img {
    width: 90%;
  }

  .modal-carousel-container {
    width: 38%;
  }
}
.cart {
  display: none;
  position: absolute;
  top: 4.8rem;
  width: 95vw;
  margin-inline: 0.5rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 0.5rem;
  box-shadow: 2px 15px 18px hsla(0deg, 0%, 0%, 0.3);
}

.cart-title {
  padding: 1rem 0 1.5rem 1rem;
  border-bottom: 1px solid hsl(220deg, 14%, 75%);
  font-size: 0.7rem;
}

.cart-empty-container {
  padding: 4rem;
  text-align: center;
  font-weight: 700;
  color: hsl(219deg, 9%, 45%);
}

.calc-quantity-container {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}

.cart-info-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.cart-info-container .cart-thumbnail {
  width: 15vw;
  border-radius: 0.3rem;
}

.quantity-info-container p {
  color: hsl(219deg, 9%, 45%);
}
.quantity-info-container p span {
  color: hsl(220deg, 13%, 13%) !important;
  font-weight: 700;
}

.checkout-container {
  display: flex;
  justify-content: center;
}

.checkout {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  padding: 1rem 0.5rem;
  width: 95%;
  background-color: hsl(26deg, 100%, 55%);
  color: hsl(0deg, 0%, 100%);
  border: none;
  border-radius: 0.8rem;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 2px 15px 8px hsl(25deg, 100%, 94%);
  transition: 300ms;
}

.checkout:hover {
  opacity: 0.7;
}

.delete {
  cursor: pointer;
}

.open-close {
  display: block;
}

@media (min-width: 1200px) {
  .cart {
    width: 35vw;
    top: 4.2rem;
    right: 4rem;
  }

  .cart-info-container .cart-thumbnail {
    width: 7vw;
  }

  .quantity-info-container p {
    font-size: 1rem;
  }

  .delete {
    width: 1rem;
  }
}
@media (min-width: 1440px) {
  .cart {
    width: 25vw;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Kumbh Sans", sans-serif;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.App {
  width: 100vw;
  height: fit-content;
  overflow-x: hidden;
  overflow-y: hidden;
}

